@import '../../../../assets/styles/variables.scss';
@import '../../../../assets/fonts/fonts.scss';

@media screen and (min-width: 769px) {
.root {
  position: relative;
  padding-top: calc(124 * $ratio-big);
  caret-color: transparent;
  cursor: default;

  font-family: 'Comfortaa';
  font-weight: 700;
  font-size: calc(18 * $ratio-big);
  line-height: calc(21 * $ratio-big);

  .navigate {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: left;
    margin: calc(25 * $ratio-big) calc(50 * $ratio-big);
    width: 100%;

    font-family: 'Comfortaa';
    font-weight: 400;
    font-size: calc(10 * $ratio-big);
    line-height: calc(16 * $ratio-big);

    .buttonNavigate {
      border: none;
      background: none;
      --myColor1: #000000;

      .icon path {
        fill: var(--myColor1);
      }

      .icon {
        width: calc(25 * $ratio-big);
        height: calc(25 * $ratio-big);
      }
    }

    .buttonNavigate:active {
      --myColor1: #969696;
      color: $color-header-info;
      text-decoration: underline;
    }

    .buttonNavigate:hover {
      --myColor1: #969696;
      color: $color-header-info;
      text-decoration: underline;
    }
  }
  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: calc(25 * $ratio-big);

    .form {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: start;
      width: calc(800 * $ratio-big);
      height: calc(500 * $ratio-big);
      padding: calc(50 * $ratio-big);

      box-shadow: calc(0 * $ratio-big) calc(5 * $ratio-big) calc(45 * $ratio-big)
        rgba(0, 0, 0, 0.25);
      border-radius: calc(25 * $ratio-big);

      .title {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: calc(35 * $ratio-big);

        font-weight: 700;
        font-size: calc(28 * $ratio-big);
        line-height: calc(28 * $ratio-big);
      }

      .button {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        border: none;
        background: none;
        border: 2px solid $color-header-info;
        border-radius: 1%;
        width: 75%;
        height: calc(75 * $ratio-big);
        color: $color-white;
        padding: 0;
        --myColor1: #ffffff;
        background-color: $color-header-navbar-hover;
      }
      .button:active {
        border-color: $color-header-info;
        color: $color-header-info;
        background-color: $color-white;
      }
      .button:hover {
        border-color: $color-header-info;
        color: $color-header-info;
        background-color: $color-white;
      }
    }
    .title {
      width: 100%;
      display: column;
      align-items: center;
      justify-content: left;
      padding-left: calc(50 * $ratio-big);
      font-size: calc(20 * $ratio-big);
      line-height: calc(24 * $ratio-big);

      .buttonNavigate {
        border: none;
        background: none;
        font-size: calc(20 * $ratio-big);
        line-height: calc(24 * $ratio-big);
        padding-left: calc(50 * $ratio-big);
      }
      .buttonNavigate:active {
        color: $color-header-info;
        text-decoration: underline;
      }

      .buttonNavigate:hover {
        color: $color-header-info;
        text-decoration: underline;
      }
    }
  }
}
}

